@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
* {
  font-family: 'Roboto Condensed', sans-serif;
}


:root {
  --primary-white: #fff;
  --primary-orange: #f9ab00;
  --primary-dark: rgba(255,255,255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  }

.App {
  text-align: center;
}

body {
  font-family: 'Roboto Condensed', sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/********NavBar******/

nav {
  z-index: 2;
}

.navbar-nav{
  margin-left: auto;
  
}

.logo {
  width: 4rem;
  margin-top: 0.3rem;
  border-radius: 50%;
}

nav a.nav-link {
  font-weight: lighter;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin:0.5rem;
  color: #fff !important ;
  color: var(--primary-white) !important ;
  border-bottom: 0.1875rem solid #212529;
}


.navbar-nav > li > a:hover {
  border-bottom: 0.1875rem solid #f9ab00;
  border-bottom: 0.1875rem solid var(--primary-orange);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 5rem;
}


@media(max-width: 768px){
 .navbar-brand {
   width: 7rem;
 }
}





/************* Home ************/

.home-wrapper {
  position: relative;
  background: url(/Portfolio/static/media/wallpaper-mania.com_High_resolution_wallpaper_background_ID_77701863050.19c8ed73.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 82vh;
  z-index: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}



.main-info {
  text-align: center;
  
}

.main-info h1 {
  color: #f9ab00;
  color: var(--primary-orange);
  text-transform: uppercase;
}



/* =============== Typed Text ===============*/ 

.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
  font-family: 'Roboto Condensed', sans-serif;
}


@media(max-width: 768px){
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

.btn-main-offer {
  border: 1px solid #ef4035;
  border: 1px solid var(--primary-red) ;
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem  0.75rem  0.75rem;
  color: #fff;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #f33501;
  background-color: var(--primary-hover-red);
  color: #fff;
  color: var(--primary-white);
  transition: .2s ease-in-out;
} 


.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}


@media (max-width: 768px) {
  .particles {
    display: none;
  }
}



/* =============== Other Pages Bg ===============*/ 

.wrapper {
  background: url(/Portfolio/static/media/background.fa6dca5a.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 82vh;
  display: flex;
  align-items: center;
  overflow-y: auto;
}
.cvBtn {
  margin-top: 1rem;
 }

 .cvBtn a {
  background-color: gray;
  margin: 1rem;
 }

 .particles {
   width: 50px;
   height: 50px;
 }


/* About Me 
---------------------*/

.aboutme-container {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 1rem;
}

.about-text {
  margin: 1rem;
}

.about-text h3 {
font-size: 45px;
font-weight: 700;
margin: 0 0 6px;
}


.about-text h6 {
font-weight: 600;
margin-bottom: 15px;
}

.about-text p {
font-size: 18px;
max-width: 450px;

}

.about-avatar {
  max-width: 22rem;
  margin: 1rem;
}

.about-avatar img {
  border-radius: 50%;
  width: 100%;
}


.theme-color {
  color: rgb(107, 107, 107);
}
.dark-color {
  color: black;
  border-bottom: #f9ab00 solid 2px;
}


@media (max-width: 767px) {
  .about-text h3 {
    font-size: 35px;
  }
  .about-text h6 {
    font-size: 18px;
  }
}


.card {
  margin: 2rem;
  max-height: 32rem;
}

@media (max-width: 575px) {
  .card-group {
    margin-top: 45rem;
  }
}

.card-body {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.btn {
  margin: 0 .8rem;
}
/* * {
  outline: 1px solid red;
} */

.form-box {
  width: 500px;
  margin: 0 auto;
  padding: 50px;
  background: #ffffff;
  border: 10px solid #f2f2f2;
}

.form-box h1, .form-box p {
  text-align: center;
}

input, textarea {
  width: 100%;
  margin-bottom: 1rem;
}
footer {
  width: 100%;
  height: 3.5rem;
  background: #212529;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
}

footer a {
  background-color: white;
  display: flex;
  height: 2.5rem;
  width: 2.5rem;
  margin: 0 10px;
  border-radius: 50%;
  align-items: center;
  justify-content: center;
  text-decoration: none;
}

footer i {
  font-size: 1.4rem;
  color: rgb(100, 100, 100);
}

footer span {
  color: white; 
  position: absolute;
  margin-right: 2rem;
  right: 0;
}


@media only screen and (max-width: 650px){
  footer span {
    display: none;
  }
}



